  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
  height: 582px;
}

.py-185 {
  padding-top: 185px;
  padding-bottom: 185px;
}

.fullBodyWorkout {
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/yoga-week8/photo-1593810450967-f9c42742e326.jpeg?raw=true);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 420px;
}

.fullBodyWorkout h5 {
  padding-top: 314px;
}

.decompression {
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/yoga-week8/photo-1593810451137-5dc55105dace.jpeg?raw=true);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 420px;
}

.decompression h5 {
  padding-top: 314px;
}

.fitness {
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/yoga-week8/photo-1599901860904-17e6ed7083a0.jpeg?raw=true);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 420px;
}

