}

@media (min-width: 768px) {
  .fullBodyWorkout, .decompression, .fitness {
    height: 252px;
  }
  .fullBodyWorkout h5, .decompression h5, .fitness h5 {
    padding-top: 136px;
  }
  .ml-off {
    margin-left: -95px;
  }
  .py-145 {
    padding-top: 145px;
    padding-bottom: 145px;
  }
}

@media (min-width: 992px) {
  .fullBodyWorkout, .decompression, .fitness {
    height: 420px;
  }
  .fullBodyWorkout h5, .decompression h5, .fitness h5 {
    padding-top: 314px;
  }
}

@media (min-width: 576px) {
  .sm-size {
    height: 210px;
  }
}

.swiper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
